//FOOTER
footer {
  background-color: $r2_color;
  padding-top: 1.25rem;

  .MuiContainer-root {
    .MuiBox-root {
      .MuiGrid-root {
        padding-top: 3rem !important;

        //COLUMN ONE
        .footer-one {
          .btn-home {
            align-self: start;
            border: none;
            color: $white;
            box-shadow: none !important;
            margin-top: -14px;

            &:hover,
            &:focus {
              background-color: $r2_color;
              border: none !important;
              box-shadow: none !important;
            }
          }
          @include responsive(sm) {
            text-align: center;
          }

          .MuiTypography-root {
            color: $white;
            font-size: 13px;
            font-weight: 100;
            font-family: $poppins_regular;
            padding-top: 10px;
          }

          .flex-one,
          .flex-two {
            align-self: start;
            display: flex;
            padding-top: 0.5rem;
            font-size: 13px;

            .MuiButton-root {
              align-self: start;
              text-transform: lowercase;
              box-shadow: none !important;
              border: none;
              color: $r2_color;

              @include responsive(sm) {
                align-self: center;
                text-align: center;
              }

              &:hover,
              &:focus {
                box-shadow: none;
                background-color: transparent;
                border: 0;
              }
            }

            a {
              text-decoration: none;
              color: $white;
              font-size: 16.5px;
              padding-top: 4px;
              padding-left: 6px;
              font-family: $poppins_regular;

              @include responsive(sm) {
                font-size: 15px;
              }

              &:hover,
              &:focus {
                box-shadow: none !important;
              }
            }

            svg {
              color: $white;
            }
          }

          .flex-two {
            margin-top: -8px;
            flex-direction: row;

            @include responsive(sm) {
              justify-content: center;
            }

            .MuiButton-root {
              margin-left: -20px;

              @include responsive(sm) {
                margin-left: -16px;
                justify-content: flex-end;
              }
            }
          }

          .flex-one {
            margin-left: -10px;
            margin-top: -8px;
            flex-direction: column;

            @include responsive(sm) {
              margin-left: 0;
            }
          }
        }

        //COLUMN TWO AND THREE
        .footer-two,
        .footer-three {
          display: flex;
          flex-direction: column;

          @include responsive(sm) {
            text-align: center;
          }

          .MuiButton-text {
            align-self: start;
            border: none;
            color: $r2_color;
            box-shadow: none !important;

            @include responsive(sm) {
              align-self: center;
            }

            &:hover,
            &:focus {
              background-color: $r2_color;
              border: none !important;
              box-shadow: none !important;
            }

            a {
              color: $white;
              text-transform: capitalize !important;
              font-family: $poppins_regular;
              font-size: 16.5px;
              text-decoration: none;
              border: none;
              box-shadow: none !important;

              @include responsive(sm) {
                font-size: 15px;
              }

              &:hover,
              &:focus,
              &:active {
                color: $r1_color;
                box-shadow: none !important;
                background-color: transparent !important;
              }
            }
          }
        }

        //COLUMN FOUR
        .footer-four {
          @include responsive(sm) {
            text-align: center;
          }

          .MuiTypography-root {
            color: $white;
            font-size: 13px;
            font-weight: 100;
            padding-top: 0.7rem;
          }

          .MuiFilledInput-root {
            background-color: $r2_color;
            padding-top: 0.75rem;

            .MuiInputBase-input {
              border-radius: 19px !important;
              background-color: $white;
              color: $gray;
              padding-top: 0.25rem;
              padding-bottom: 0.25rem;
              font-size: 15px;
            }
          }

          .email-address,
          .full-name,
          .message {
            padding-top: 0.25rem;
            display: flex;

            @include responsive(sm) {
              justify-content: center;
            }

            .MuiInput-root {
              background-color: $white;
              width: 75%;
              border-radius: 9px;
              border: none;
              padding-left: 1.1rem;
              font-family: $poppins_regular;

              &:after,
              &:before {
                border-bottom: none !important;
              }
            }
          }

          .error {
            color: $light_pink;
            font-size: 13px;
          }

          .send {
            padding-top: 0.25rem;

            .btn-send {
              background: $linear;
              border-radius: 9px;
              font-weight: 800;
              border: 0;
              width: 75%;
              color: $r1_color;
              padding-top: 0.45rem;
              padding-bottom: 0.35rem;
            }
          }
        }

        .MuiPaper-root {
          font-size: 21px;
          color: $white;
          box-shadow: none !important;
          font-weight: 600;
          padding: 0;
          font-family: $poppins_regular;
        }

        .footer-five {
          text-align: center;

          .copyright {
            padding-top: 1rem;
            color: $white;
            font-size: 13px;
            font-weight: 100;
          }
        }
      }
    }
  }
}
