:root {
  --text-color: black;
  --background-color: white !important;
  --a-color: $r1_color;
}

.dark {
  --text-color: white;
  --background-color: #1d1e1f;
  --a-color: $r1_color;
}

.MuiCard-root,
.MuiTableRow-root,
li,
h1,
td,
h2,
h3,
h4,
h5,
h6,
.caption,
input,
.feel-free,
.MuiInputLabel,
input,
.MuiOutlinedInput-root,
.list-item,
.label,
.nav-link,
.menu-icon,
.MuiAccordionDetails-root .MuiTypography-root,
.settings-icon,
.MuiTablePagination-select,
.MuiTablePagination-displayedRows,
svg[data-testid="ArrowDropDownIcon"],
svg[data-testid="KeyboardArrowRightIcon"],
svg[data-testid="KeyboardArrowLeftIcon"] {
  color: var(--text-color) !important;
}

body,
.MuiTableCell-root,
.box,
.MuiPaper-root,
.Muibox-root,
ul,
.item,
.main-box,
input {
  background-color: var(--background-color) !important;
}

footer .MuiContainer-root .MuiBox-root .MuiGrid-root .MuiPaper-root {
  background-color: $r2_color  !important;
}

svg[preserveAspectRatio="xMidYMid meet"] {
  width: 50% !important;
}

.MuiMenuItem-root {
  &:hover {
    background-color: transparent !important;
  }
}

.css-10by3a5-MuiButtonBase-root-MuiMenuItem-root-MuiTablePagination-menuItem.Mui-selected {
  background-color: transparent !important;

  &:hover,
  &:focus {
    background-color: transparent !important;
  }
}
