@mixin responsive($breakpoint) {
    
    @if $breakpoint==xs {
        @media (max-width:0px) {
            @content
        }

        ;
    }

    @if $breakpoint==sm {
        @media (max-width:600px) {
            @content
        }

        ;
    }

    @if $breakpoint==md {
        @media (max-width:900px) {
            @content
        }

        ;
    }

    @if $breakpoint==lg {
        @media (max-width: 1200px) {
            @content
        }

        ;
    }

    @if $breakpoint==xl {
        @media (max-width: 1536px) {
            @content
        }

        ;
    }

        @if $breakpoint==xxl {
            @media (max-width: 2000px) {
                @content
            }

            ;
        }
}