   //HEADER
   header {

       .main-box {
           display: flex;

           .MuiPaper-root {
               background-color: $white;
               color: $white;
               box-shadow: none;
               border: none;

               .btn-home {
                   align-self: start;
                   margin-left: -40px;
                   margin-top: 6px;
                   border: none;
                   box-shadow: none !important;

                   &:hover,
                   &:focus {
                       background-color: transparent;
                       border: none !important;
                       box-shadow: none !important;
                   }
               }

               .MuiContainer-root {
                   .MuiToolbar-root {
                       padding-left: 0;


                       .menu-btn {
                           @include responsive(xxl) {
                               display: none !important;
                           }

                           @include responsive(md) {
                               display: inline-block !important;
                               text-align: end;
                               order: 1;
                               flex-grow: 1;
                           }

                           .MuiIconButton-root {
                               color: $black;

                               &:hover {
                                   background-color: transparent;

                               }
                           }
                       }


                       .MuiTypography-root {
                           flex-grow: 0;
                           line-height: 0 !important;
                           padding-left: 0;

                           @include responsive(md) {
                               text-align: start;
                               order: 0;
                           }

                           img {

                               padding-top: 5px;

                               @include responsive(md) {
                                   margin-left: -35px;
                               }
                           }
                       }

                       .item-box {

                           @include responsive(xl) {
                               display: block;
                           }

                           @include responsive(md) {
                               display: none !important;
                           }



                           .MuiButton-text {
                               border: none;
                               color: white;
                               box-shadow: none !important;
                               padding: 0;

                               &:hover,
                               &:focus {
                                   background-color: transparent;
                                   border: none !important;
                                   box-shadow: none !important;
                               }

                               .active {
                                   color: $r1_color !important;
                                   background-color: transparent;

                               }

                               .nav-link {
                                   color: $black;
                                   text-transform: capitalize !important;
                                   font-weight: 600;
                                   font-size: 18px;
                                   padding: 2px 14px;
                                   text-decoration: none;
                                   font-family: $poppins_regular;

                                   border: none;

                                   @include responsive(lg) {
                                       font-size: 14.6px;
                                       padding: 1px 8px;
                                   }

                                   &:hover,
                                   &:focus {
                                       background-color: transparent !important;
                                       color: $r1_color !important;
                                       box-shadow: unset !important;
                                   }
                               }
                           }
                       }
                   }
               }
           }
       }
   }





   //DRAWER MODAL -- MOBILE
   .drawer {

       .MuiPaper-root {
           height: 100%;
           box-sizing: border-box;

           .MuiContainer-root {
               width: 300px;
               padding-top: 5px;



               .main-box {
                   .MuiTypography-root {
                       .logo {
                           margin-left: -30px;

                       }
                   }

                   .btn-home {
                       align-self: start;
                       margin-left: -40px;
                       border: none;
                       margin-top: 6px;

                       &:hover,
                       &:focus {
                           background-color: transparent !important;
                           border: none !important;
                           box-shadow: none !important;
                       }


                   }

                   .item-box {
                       display: flex;
                       flex-direction: column;
                       padding-top: 20px;



                       .active {
                           color: $r1_color !important;
                           background-color: transparent;



                           &:hover,
                           &:focus {
                               background-color: transparent !important;
                               color: $r1_color !important;

                           }
                       }

                       .MuiButton-text {
                           align-self: start;
                           border: none;
                           color: $white;
                           box-shadow: none !important;

                           &:hover,
                           &:focus {
                               background-color: transparent;
                               border: none !important;
                               box-shadow: none !important;
                           }

                           .nav-link {
                               color: $black;
                               text-transform: capitalize !important;
                               font-weight: 600;
                               font-size: 20px;
                               text-decoration: none;
                               font-family: $poppins_regular;
                               border: none;


                               &:hover,
                               &:focus {
                                   background-color: transparent;
                                   color: $r1_color;

                               }
                           }
                       }
                   }
               }
           }
       }
   }

   .dark-mode-desktop {
    @include responsive(md){
        display: none;
    }
   }