.container {
  //MAIN TITLE
  .MuiGrid-root {
    .MuiGrid-item {
      padding-top: 7px;

      .main-title {
        color: $r2_color;
      }
    }
  }

  //BOX PROPS
  .box {
    .container {
      padding-bottom: 20px;
      padding-top: 20px;
      .children {
        .item {
          font-family: $poppins_regular;
          padding-left: 24px;
          padding-right: 24px;
          border-radius: 0;

          @include responsive(sm) {
            padding-left: 12px;
            padding-right: 12px;
          }

          .col,
          .col1 {
            width: 100%;

            .label {
              font-family: $poppins_regular !important;
              font-size: 28px;
              font-weight: 600;
              text-transform: uppercase;
              color: $black;

              @include responsive(sm) {
                font-size: 23.5px;
              }
            }

            .MuiButton-root {
              &:hover,
              &:focus {
                box-shadow: none !important;
                background-color: transparent !important;
              }
            }
          }

          //OBJECTIVES
          .objectives,
          .education,
          .about,
          .languages,
          .hobbies,
          .contact {
            .caption {
              font-size: 17px;
              color: $black;
              font-family: $poppins_regular;

              @include responsive(sm) {
                font-size: 13.6px;
              }
            }
            .MuiGrid-container {
              .MuiGrid-root {
                .css-1k9ek97 {
                  .MuiList-root {
                    padding-top: 0 !important;
                    padding-bottom: 0 !important;

                    li {
                      padding-left: 0;
                      padding-top: 0;

                      a {
                        text-decoration: none;
                        color: $black;
                      }

                      .MuiListItemIcon-root {
                        min-width: 35px;

                        .MuiSvgIcon-root {
                          color: $r1_color;
                        }
                      }

                      .MuiListItemText-root {
                        .MuiTypography-root {
                          span {
                            font-weight: 600;
                            color: $r2_color;
                          }

                          font-family: $poppins_regular !important;
                          font-size: 17px;

                          @include responsive(sm) {
                            font-size: 13.6px;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }

          .about {
            .business-card {
              padding-top: 0.1rem;
              padding-bottom: 0.1rem;
              color: $r1_color;
              border: 1px solid $r1_color;
              font-family: $poppins_regular;

              &:hover {
                background-color: rgba(255, 0, 0, 0.171);
              }

              a {
                color: $r1_color;
                text-decoration: none;
              }

              .business-card-icon {
                padding-top: 4px;
              }
            }
          }

          .skills {
            .MuiTableHead-root {
              .MuiTableCell-root {
                font-family: $poppins_regular;
              }
            }

            .MuiTableBody-root {
              .MuiTableCell-root {
                font-family: $poppins_regular;

                @include responsive(sm) {
                  font-size: 15px;
                }
              }
            }

            .MuiTablePagination-selectLabel {
              font-family: $poppins_regular;
              font-weight: 600;
              font-size: 15px;
              color: $r2_color;
            }

            .MuiTablePagination-displayedRows,
            .MuiInputBase-input {
              font-family: $poppins_regular;
            }

            .MuiToolbar-root {
              svg {
                path {
                  color: $r1_color;
                }
              }
            }
          }

          .projects {
            .MuiContainer-root {
              padding-left: 0;
              padding-right: 0;

              .MuiGrid-container {
                .coming-soon {
                  color: $r1_color;
                  font-family: $poppins_regular;
                  font-weight: 600;
                  font-size: 14px;
                }

                .MuiGrid-item {
                  width: 100%;

                  .MuiPaper-root {
                    height: 100% !important;
                    border-radius: 0 !important;

                    .img-wrap {
                      text-align: -webkit-center;
                      padding-top: 0.7rem;

                      img {
                        height: auto;
                        width: auto;
                      }
                    }

                    .MuiCardContent-root {
                      .project-name {
                        font-family: $poppins_regular;
                        color: $r2_color;
                        font-weight: 600;
                        font-size: 24px;

                        @include responsive(sm) {
                          font-size: 21px;
                        }
                      }

                      .MuiTypography-body2 {
                        font-family: $poppins_regular;
                        font-size: 0.95rem;

                        @include responsive(sm) {
                          font-size: 13.6px;
                        }
                      }
                    }

                    .MuiCardActions-root {
                      .MuiButton-root {
                        border: 1px solid $r1_color !important;
                        color: $r1_color !important;

                        &:hover {
                          background-color: rgba(255, 0, 0, 0.171);
                        }

                        a {
                          text-decoration: none;
                          font-family: $poppins_regular;
                          font-size: 14px;
                          color: $r1_color;
                          text-transform: capitalize;
                        }
                      }
                    }
                  }
                }
              }
            }
          }

          .experiences {
            .MuiContainer-root {
              padding-left: 0;
              padding-right: 0;

              .MuiGrid-container {
                padding-top: 1rem;
                padding-bottom: 1rem;

                .MuiPaper-root {
                  border-radius: 0 !important;

                  .MuiButtonBase-root {
                    .job-title {
                      color: $r2_color;
                      font-family: $poppins_regular;
                      font-weight: 600;
                      font-size: 18.5px;

                      @include responsive(sm) {
                        font-size: 15px;
                      }
                    }
                  }

                  .MuiCollapse-wrapper {
                    .MuiTypography-root {
                      font-family: $poppins_regular;
                      font-weight: 600;
                      color: $black;
                      font-size: 16.5px;

                      @include responsive(sm) {
                        font-size: 13.6px;
                      }
                    }
                    ul li::marker {
                      color: $r2_color;
                    }
                    ul {
                      padding-left: 25px;

                      li {
                        font-size: 16px;

                        @include responsive(sm) {
                          font-size: 13.6px;
                        }
                      }
                    }
                  }

                  .MuiAccordionSummary-root {
                    svg {
                      color: $r1_color;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

.not-found {
  .MuiGrid-root {
    @include responsive(md) {
      justify-content: center;
    }
  }
}

.contact {
  .MuiGrid-root {
    .button-col {
      text-align: center;
      .MuiButtonBase-root {
        font-weight: 700;
        background-color: $r2_color;
        color: $white;
        padding-top: 9px;
        &:hover {
          background-color: $r1_color;
        }
      }
    }

    .error,
    .validation-message {
      color: $r1_color;
      font-weight: 700;
    }

    textarea {
      height: 100px !important;
    }

    .MuiFormControl-root {
      width: 100%;

      .MuiInputLabel,
      .MuiInputBase-input,
      .MuiInputLabel-root.Mui-focused {
        font-family: $poppins_regular;
        font-weight: 600;
      }

      label,
      input {
        font-family: $poppins_regular;
      }

      fieldset {
        border-color: $r2_color;
        border-width: 2px;

        @include responsive(lg) {
          border-width: 2px;
        }
      }
    }
  }
}

.PhoneInput {
  display: flex;
  align-items: center;
  border: 2px solid $r2_color;
  border-radius: 4px;
  height: 1.2em;
  padding: 16px 15px;
}

.PhoneInputInput {
  flex: 1;
  min-width: 0;
  outline: none;
  font-family: $poppins_regular;
  border: 0;
  font-weight: 600;
  font-size: 1rem;
}

.PhoneInputCountryIcon {
  width: calc(1.3em * 1.5);
  height: 1.3em;
  border: 0;
}

.PhoneInputCountryIcon--square {
  width: 1em;
}

.PhoneInputCountryIcon--border {
  background-color: transparent;
  box-shadow: none;
}

.PhoneInputCountryIconImg {
  display: block;
  width: 100%;
  height: 100%;
}

.PhoneInputInternationalIconPhone {
  opacity: 0.8;
}

.PhoneInputInternationalIconGlobe {
  opacity: 0.65;
}

.PhoneInputCountry {
  position: relative;
  align-self: stretch;
  display: flex;
  align-items: center;
  margin-right: 0.35em;
}

.PhoneInputCountrySelect {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
  opacity: 0;

  option {
    background-color: $white;
    color: $black;
    font-size: 1rem;
    font-family: $poppins_regular;
  }
}

.PhoneInputCountrySelectArrow {
  display: block;
  content: "";
  width: 0.4em;
  height: 0.4em;
  margin-left: 0.55em;
  border-style: solid;
  border-color: $r2_color;
  border-top-width: 0;
  border-bottom-width: 3px;
  border-left-width: 0;
  border-right-width: 3px;
  transform: rotate(45deg);
  opacity: 0.45;
}

.PhoneInputCountrySelect:focus
  + .PhoneInputCountryIcon
  + .PhoneInputCountrySelectArrow {
  opacity: 1;
  color: $r2_color;
}

.PhoneInputCountrySelect:focus + .PhoneInputCountryIcon--border {
  box-shadow: none;
}

.PhoneInputCountrySelect:focus
  + .PhoneInputCountryIcon
  .PhoneInputInternationalIconGlobe {
  opacity: 1;
  color: transparent;
}
